import axios from "./axios";
import moment from "moment";

function convertDate(r: any) {
  return {
    ...r,
    tanggal: moment(r.tanggal, "YYYY-MM-DD")
  }
}

export default {
  find: () => {
    return axios.get("/transaksi")
      .then(resp => {
        return resp.data;
      })
      .then(data => data.map(convertDate));
  },
  findOne: (id: number) => {
    return axios.get(`/transaksi/${id}`)
      .then(resp => {
        console.log(resp);
        console.log(resp.data);
        return resp.data;
      })
      .then(convertDate)
  },
  create: (payload: any) => {
    return axios.post("/transaksi", payload)
      .then(resp => resp.data)
      .then(data => data.map(convertDate))
  },
  update: (id: number, payload: any) => {
    return axios.put(`/transaksi/${id}`, payload)
      .then(resp => resp.data)
      .then(data => data.map(convertDate))
  },
  delete: (id: number) => {
    return axios.delete(`/transaksi/${id}`)
  },
  lastTransaksi: () => {
    return axios.get(`/last-transaksi`).then(resp => resp.data)
  }
}
