<template>
    <v-container
    fluid
    grid-list-md
  >
    <v-toolbar
      flat
    >
      <v-spacer/>
      <v-toolbar-title class="ev-russo">Form Penentuan</v-toolbar-title>
      <v-spacer/>
    </v-toolbar>
    
    <v-layout row>
      <v-flex md6>
        <v-card class="my-4" flat>
          <v-card-text>
            <v-text-field
              readonly
              label="Produksi"
              :value="lastTrans.produksi"
            />
            <v-text-field
              label="Penjualan"
              readonly
              :value="lastTrans.penjualan"
            />
            <v-text-field
              readonly
              label="Persediaan"
              :value="lastTrans.persediaan"
            />
          </v-card-text>
        </v-card>
      </v-flex>
      <v-flex md6>
        <v-card class="my-4" flat
          :dark="isSuccess"
          :color="isSuccess ? 'blue darken-2' : 'white'"
          :class="{ 'white--text': isSuccess }"
        >
          <v-card-text>
            <v-text-field
              type="number"
              min="0"
              :readonly="isSuccess"
              label="Produksi"
              v-model.number="item.produksi"
            />
            <v-text-field
              type="number"
              min="0"
              label="Penjualan"
              :readonly="isSuccess"
              v-model.number="item.penjualan"
            />
            <v-text-field
              type="number"
              min="0"
              :readonly="isSuccess"
              label="Persediaan"
              v-model.number="item.persediaan"
            />
          </v-card-text>
        </v-card>
        <v-btn
          v-if="state === 'idle'"
          @click="predict"
          large
          dark
          block
          depressed
          color="cyan darken-1"
        >
          Jalankan
        </v-btn>

        <v-card
          v-else
          class="my-2 text-xs-center"
        >
          <v-card-text>
            <div class="ev-russo display-1">
              {{ parseInt(result.defuzz) }} buah
            </div>
            <div class="ev-russo display-2">
              {{ result.label }}
            </div>
          </v-card-text>

          <v-card-action>
            <v-btn
              @click="state = 'idle'"
              large
              dark
              block
              depressed
              color="cyan darken-1"
            >
              Ulangi
            </v-btn>
          </v-card-action>
        </v-card>

      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import Vue from "vue";
import evbus from "@/evbus";
import defaults from "@/defaults";
import MamdaniAPI from "@/services/mamdani";
import RuleAPI from "@/services/rule";
import TransaksiAPI from "@/services/transaksi";

const _defaultFormValues = {
  produksi: 0,
  penjualan: 0,
  persediaan: 0
}

export default Vue.extend({
  name: "Prediksi",
  data: () => ({
    item: { ..._defaultFormValues },
    lastTrans: { ..._defaultFormValues },
    state: 'idle',
    result: {
      defuzz: 0,
      label: undefined
    }
  }),
  computed: {
    isSuccess () {
      return this.state === 'success';
    },
  },
  methods: {
    async predict() {
      const payload = [
        this.item.produksi,
        this.item.penjualan,
        this.item.persediaan
      ];
      evbus.$emit("loading-on");
      try {
        const result = await MamdaniAPI.run(payload);
        this.result = result;
        this.result.label = RuleAPI.ruleLevel(result.label);
        this.state = 'success';
      } catch (err) {
        console.log(err);
        this.state = 'error';
      } finally {
        evbus.$emit("loading-off");
      }
    }
  },
  mounted() {
    TransaksiAPI.lastTransaksi()
      .then(item => {
        this.lastTrans = { ...item }
      })
      .catch(err => {
        alert("Gagal mengambil data transaksi terakhir")
      })
  }
});
</script>

