import axios from "./axios";

function _ruleLevel(r: number) {
  switch (r) {
    case 0: return "Rendah";
    case 1: return "Sedang";
    case 2: return "Tinggi";
    default:
      throw new Error(`Can't find matching pattern for: ${r}`);
  }
}

function _displayRule(r: any) {
  const display = {
    produksi: _ruleLevel(r.produksi),
    penjualan: _ruleLevel(r.penjualan),
    persediaan: _ruleLevel(r.persediaan),
    permintaan: _ruleLevel(r.permintaan)
  };
  return {
    ...r,
    display
  };
}

export default {

  find: () => {
    return axios.get("/rules")
      .then(resp => resp.data)
      .then(data => {
        const items = data.items.map(_displayRule);
        const counts = data.counts;
        return {
          items,
          counts
        };
      });
  },
  findOne: (id: number) => {
    return axios.get(`/rules/${id}`)
      .then(resp => resp.data)
      .then(data => _displayRule(data));
  },
  create: (payload: any) => {
    return axios.post("/rules", payload)
      .then(resp => resp.data)
      .then(_displayRule);
  },
  update: (id: number, payload: any) => {
    return axios.put(`/rules/${id}`, payload)
      .then(resp => resp.data)
      .then(_displayRule);
  },
  delete: (id: number) => {
    return axios.delete(`/rules/${id}`);
  },

  ruleLevel: _ruleLevel

}